.news {
    padding-top: 96px;
    padding-bottom: 96px;
    text-align: center;
    background-color: #EEEEEE;
}

.news__title {
    color: black;

    &::after {
        border-color: black;
    }
}

.news__list {
    @include list-reset;
    @include flex;
    flex-direction: column;
    margin-top: 0;
    margin-bottom: 0;
    text-align: left;
    gap: 50px;

    @media (min-width: 993px) {
        flex-direction: row;
        justify-content: center;
        align-items: flex-start;
        gap: 30px;
      }
}

.news__list--link {
    @include flex(flex-start);
    flex-direction: column;
    color: black;
    max-width: 690px;

    @media (min-width: 993px) {
        max-width: 300px;
        height: auto;

        &:hover {
            transform: scale(1.1);
            transition: 0.5s ease;
            @include box-shadow (black);
            padding: 0 2px;
            overflow: hidden;
      }
      }

      @media (min-width: 1200px) {
        max-width: 350px;
        height: auto;
      }

}

.news__link--img {
    object-fit: cover;
    min-width: 240px;
    min-height: 240px;
    height: auto;
    align-self: center;
    display: block;

    @media (min-width: 575px) {
      min-height: 450px;
      width: 690px;
    }

    @media (min-width: 993px) {
        max-width: 300px;
        min-height: 290px;
      }

      @media (min-width: 1200px) {
        max-width: 350px;
        min-height: 290px;
      }
}

.news__link--title {
    @include text-size (14px, 24px);
    font-weight: 700;
    text-transform: uppercase;
    margin-top: 24px;
    margin-bottom: 0;

    @media (min-width: 575px) {
        @include text-size (16px, 24px);
      }
}

.news__link--description {
    @include text-size (14px, 24px);
    margin-top: 0;
    margin-bottom: 24px;

    @media (min-width: 575px) {
        @include text-size (16px, 24px);
      }
}

.news__link--more {
    @include text-size (12px, 24px);
    margin-top: 0;
    margin-bottom: 0;
    text-transform: capitalize;

    &::before {
        content: "-";
        margin-right: 5px;
    }
}