.testimonials {
    padding-top: 95px;
    padding-bottom: 95px;
    background-image: url(../img/testimonials/testimonials-sm.png);
    background-position: center;
    background-size: cover;
    text-align: center;
    

    @media (min-width: 768px) {
        background-image: url(../img/testimonials/testimonials-md.png);
    }

    @media (min-width: 993px) {
        background-image: url(../img/testimonials/testimonials-lg.png);
        background-position:center;
    }
}

.testimonials-list {
    @include list-reset;
    @include flex;
    margin-top: 0;
    margin-bottom: 0;
    position: relative;
    width: 100%;
    height: 250px;
}

.testimonial-list__item {
    @include flex;
    flex-direction: column;
    position: absolute;
    opacity: 0;
    z-index: 1;

    &--active {
        opacity: 1;
        transition: 0.5s ease-out;
    }
}

.testimonials__description {
    margin-bottom: 24px;
    max-width: 690px;

}

.slider {
    @include flex;
    gap: 10px;
    position: relative;
    z-index: 1;
}

.slider__item {
    width: 11px;
    height: 11px;
    border: 1px solid #ffffff;
    border-radius: 50%;
    cursor: pointer;

    &--active {
        background-color: #ffffff;
    }
}