.fresh {
    background-image: url(../img/fresh/fresh-bg-sm.png);
    background-position: center;
    background-size: cover;
    padding-top: 95px;
    padding-bottom: 95px;
    text-align: center;

    @media (min-width: 768px) {
        background-image: url(../img/fresh/fresh-bg-md.png);
    }

    @media (min-width: 993px) {
        background-image: url(../img/fresh/fresh-bg-lg.jpg);
    }
}

.fresh__title {
    margin-bottom: 24px;
    max-width: 345px;

}
