html {
    scroll-behavior: smooth;
}

.body-page {
    font-family: $main-font;
    min-width: 320px;

    &__no-scroll {
        overflow: hidden;
    }
}

.container{
    max-width: 1200px;
    margin-right: auto;
    margin-left: auto;
    padding-left: 30px;
    padding-right: 30px;
    
}

.button {
    @include text-size (16px, 48px);
    color: #FFFFFF;
    text-transform: uppercase;
    border: 1px solid #FFFFFF;
    padding: 0 40px;

    &:hover {
        @include box-shadow;
        @include text-shadow;
    }
}

.section-info {
    @include flex;
    flex-direction: column;
}

.section-title {
    @include text-size (28px, 38px);
    font-weight: 700;
    text-transform: capitalize;
    color: #FFFFFF;
    margin-top: 0;

    @media (min-width: 575px) {
        @include text-size (40px, 48px);
    }

    &::after {
        content: "";
        border-bottom:  1px solid #FFFFFF;
        padding-bottom: 20px;
        position: relative;
        width: 190px;
        left: calc(50% - 95px);
        display: block;
    }
    @media (min-width: 768px) {
        max-width: 100%;
    }
}

.section__description {
    @include text-size (16px, 24px);
    color: #FFFFFF;
    max-width: 475px;
    margin-top: 0;
    margin-bottom: 50px;

}