.location {
    padding-top: 95px;
    padding-bottom: 95px;
    text-align: center;
    background:
    linear-gradient( rgba(0, 0, 0, 0.9)), 
    url(../img/locations/locations-bg-sm.png) center;
    background-size: cover;
    color: #ffffff;

    @media (min-width: 768px) {
        background: 
        linear-gradient( rgba(0, 0, 0, 0.9)), 
        url(../img/locations/locations-bg-md.png)center;
        background-size: cover;
    }

    @media (min-width: 993px) {
        background: 
        linear-gradient( rgba(0, 0, 0, 0.9)), 
        url(../img/locations/locations-bg.png) center;
        background-size: cover;
    }
}

.location__description {
    max-width: 300px;
}

.location__list {
    @include list-reset;
    @include flex;
    flex-direction: column;
    margin-top: 0;
    margin-bottom: 0;
    gap: 50px;
    width: 100%;

    @media (min-width: 993px) {
        flex-direction: row;;
        align-items: flex-start;
        justify-content: space-around;
        gap: 0;
      
    }
}

.location__list--item {
    max-width: 255px;
}
.location__list--title {
    @include text-size (24px, 48px);
    margin-top: 24px;
    margin-bottom: 24px;
    text-transform: capitalize;
    font-weight: normal;
}

.location__list--descriprion {
    @include text-size (16px, 24px);
    margin-top: 0;
    margin-bottom: 0;
}

.description--adress {
    display: block;
}