.newsletter {
    padding-top: 95px;
    padding-bottom: 95px;
    background-color: #000000; 
    text-align: center;
}

.newsletter__form {
    @include flex;
    flex-direction: column;
    gap: 24px;

    @media (min-width: 575px) {
        flex-direction: row;
    }
}

.newsletter__form--input {
    @include text-size (16px, 48px);
    color: #FFFFFF;
    border: 1px solid #FFFFFF;
    padding: 0 40px;
    width: 275px;
    background-color: transparent;
    
    &::placeholder {
        text-transform: capitalize;
        @include text-size (16px, 48px)
    }

}
.newsletter__form--btn {
    background-color: transparent;
    cursor: pointer;
}