.why-us {
    padding-top: 95px;
    padding-bottom: 95px;
    background-image: url(../img/why-us/why-us-bg-sm.jpg);
    background-position: right;
    background-repeat: no-repeat;

    @media (min-width: 768px) {
        background-image: url(../img/why-us/why-us-bg-md.jpg);
    }

    @media (min-width: 993px) {
        padding-top: 145px;
        padding-bottom: 145px;
        background-image: url(../img/why-us/why-us-bg-lg.jpg);
        background-position:center;
    }
}

.why-us__list {
    @include list-reset;
    margin-top: 0;
    margin-bottom: 0;
    @include flex( flex-start);
    flex-direction: column;
    gap: 48px;

    @media (min-width: 993px) {
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: space-between;
    }
}

.why-us__list--item {
    @include flex(center,flex-start);
    gap: 15px;
    max-width: 350px;


    &__img {
        display: block;

        width: 44px;
        height: 44px;

        @media (min-width: 575px) {
            width: 64px;
            height: 64px;
        }
    }
    &:nth-child(odd) {

        @media (min-width: 993px) {
            flex-direction: row-reverse;
            text-align: right;
        }
    }
}

.list-item__description {
    @include flex(flex-start);
    flex-direction: column;

}

.list-item__title {
    width: 100%;;
    text-transform: uppercase;
    @include text-size (14px, 20px);
    margin-top: 0;
    margin-bottom: 0;

    @media (min-width: 575px) {
        @include text-size (16px, 24px);
    }
}

.list-item__text {
    @include text-size (10px, 18px);
    margin-top: 0;
    margin-bottom: 0;

    @media (min-width: 575px) {
        @include text-size (12px, 24px);
    }
}