.header-page {
    padding-top: 48px;
    position: fixed;
    min-width: 320px;
    width: 100%;
    z-index: 3;

    &--fixed {
        padding-top: 10px;
        padding-bottom: 10px;
        background-color: rgba(0, 0, 0, 0.9);

        @media (min-width: 993px) {
            padding-top: 20px;
            padding-bottom: 20px;
        }
    }
    
}

.header-navbar {
    position: relative;

    @media (min-width: 993px) {
        position: static;
        @include flex (center, space-between)
    }
}

.header-logo {
    @include text-size (16px, 24px);
    text-transform: uppercase;
    color: #EEEEEE;
    position: relative;
    z-index: 4;

    &:hover {
        @include text-shadow;
    }
    @media (min-width: 993px) {
        position: static;
    }
}

.menu__list {
    @include list-reset;
    margin-top: 0;
    display: none;
    gap: 18px;
    margin-bottom: 0;
    

    &--active {
        @include flex(flex-start);
        flex-direction: column;
        position: absolute;
        padding-top: 30px;
        padding-left: 70px;
        top: -48px;
        left: -40px;
        background-color: rgba(0, 0, 0, 0.9);
        height: 120vh;
        width: 120vw;
        min-width: 320px;
    
    }
     @media (min-width: 575px) {
        padding-left: 120px;
    }

    @media (min-width: 993px) {
        position: static;
        @include flex;
        gap: 25px;
        padding-left: 0;
    }

    @media (min-width: 1200px) {
       gap: 40px;
    }
}

.menu__list--link {
    @include text-size (24px, 48px);
    text-transform: uppercase;
    color: #FFFFFF;
    display: block;
    width: 100vw;

    &:hover {
        @include text-shadow;
    }

    @media (min-width: 993px) {
        @include text-size (16px, 24px);
        width: auto;

    }
}

.burger-icon {
    position: absolute;
    width: 19px;
    height: 15px;
    right: 0;
    top: 5px;
    cursor: pointer;
    z-index: 3;

    @media (min-width: 993px) {
        display: none;
    }
}

.burger-icon__line {
    background-color: white;
    width: 19px;
    height: 1px;
    position: absolute;
    @include rounding (20px,20px,20px,20px);
    top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
    transition: 0.2s linear;

    &:nth-of-type(1) {
        top: calc(50% - 6px);
 
    }
    &:nth-of-type(3) {
        top: calc(50% + 6px);
    }
}

.burger-icon__line--active {

    &:nth-of-type(2) {
        display: none;
      }
      &:nth-of-type(1) {
        width: 100%;
        top: 50%;
        left: 50%;
        transform: translate(-50%, 0%) rotate(45deg);  
      }
      &:nth-of-type(3) {
        top: 50%;
        transform: translate(-50%, 0%) rotate(-45deg); 
      }
}