.intro {
    background-image: url(../img/intro/intro-bg-sm.png);
    background-position: center;
    background-size: cover;
    padding-top: 260px;
    padding-bottom: 260px;
    text-align: center;

    &__container {
        @include flex;
        flex-direction: column;
    }

    @media (min-width: 575px) {
        padding-top: 275px;
        padding-bottom: 275px;
    }
    @media (min-width: 768px) {
        background-image: url(../img/intro/intro-bg-md.jpg);
    }
    @media (min-width: 993px) {
        background-image: url(../img/intro/intro-bg-lg.png);
      
    }
    
}


.intro__title {
    @include text-size (22px, 76px);
    font-weight: 700;
    text-transform: capitalize;
    color: #FFFFFF;
    margin-top: 0;
    margin-bottom: 20px;

    @media (min-width: 575px) {
        @include text-size (40px, 96px);
    }

    @media (min-width: 993px) {
        @include text-size (86px, 96px);
    }

    @media (min-width: 1050px) {
        @include text-size (96px, 96px);
    }
}

.intro__description {
    @include text-size (16px, 30px);
    color: #FFFFFF;
    margin-top: 0;
    margin-bottom: 48px;
    max-width: 763px;

    @media (min-width: 575px) {
        @include text-size (24px, 48px);
    }

    @media (min-width: 993px) {
        @include text-size (32px, 40px);
    }
}