.footer-page {
    background-color: #000000;
    padding-top: 45px;
    padding-bottom: 45px;
    color: #ffffff;

    &__container {
        @include flex (center, space-between);
        flex-direction: column-reverse;
        gap: 25px;

        @media (min-width: 575px) {
            @include flex (center, space-between);
            flex-direction: row;
        }
    }
}

.footer-page__copyright {
    @include text-size (12px, 24px);
    margin-top: 0;
    margin-bottom: 0;
}

.footer-page__menu {
    display: none;
    @include list-reset;
    margin-top: 0;
    margin-bottom: 0;
    gap: 45px;
    cursor: pointer;

    &--link {
        @include text-size (16px, 26px);
        color: #ffffff;
        text-transform: capitalize;
        &:hover {
            @include text-shadow;
        }
    }

    @media (min-width: 993px) {
        @include flex;
    }
}

.footer-page__social-list {
    @include list-reset;
    margin-top: 0;
    margin-bottom: 0;
    @include flex;
    gap: 25px;
}

.social-list--item {
    background-color: #252525;
    border-radius: 50%;
    width: 24px;
    height: 24px;
    @include flex;

    &:hover {
        @include box-shadow;
        @include text-shadow;
    }
}

.social-list--link {
    color: #ffffff;
    @include text-size (8px, 24px);
    padding: 9px;
    text-transform: capitalize;
}

