.our-menu {
    background-color: #D9D9D9;
    padding-top: 95px;
    padding-bottom: 95px;
    text-align: center;
}

.our-menu__title {
    color: black;

    &::after {
        border-color: black;
    }
}

.our-menu__description {
    color: black;
}

.food-list {
    @include list-reset;
    @include flex;
    gap: 40px;
    margin-top: 0;
    margin-bottom: 50px;

    &__item {
        @include text-size (16px, 24px);
        text-transform: uppercase;
        cursor: pointer;

        &--active {
            border-bottom: 1px solid black;
            transform: scale(1.2);
            transition: .4s ease;
        }
 }
}

.menu-list {
    @include list-reset;
    @include flex(flex-start);
    flex-direction: column;
    margin-top: 0;
    margin-bottom: 0;
    gap: 30px;
    width: 100%;

    @media (min-width: 768px) {
        align-items: center;;
    }

    @media (min-width: 1200px) {
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: space-between;
    }
}

.menu-list__item {
    @include flex(center,space-between);
    text-align: left;
    gap: 20px;
    width: 100%;

    @media (min-width: 575px) {
        gap: 25px;
    }
    @media (min-width: 768px) {
        max-width:690px;
    }
    @media (min-width: 1200px) {
        max-width:545px;

    }
}

.menu-list__item--name {
    @include text-size (14px, 20px);
    text-transform: uppercase;
    font-weight: normal;
    margin-top: 0;
    margin-bottom: 0;
    @media (min-width: 575px) {
        @include text-size (16px, 24px);
    }
}

.menu-list__item--compound {
    @include text-size (10px, 18px);
    margin-top: 0;
    margin-bottom: 0;

    @media (min-width: 575px) {
        @include text-size (12px, 24px);
    }
}

.menu-list__item--price {
    align-self: flex-start;
    margin-top: 0;
    margin-bottom: 0;

    &::before {
        content: "$";
    }
}

.menu-list__item--description {
    width: 100%;
    @media (min-width: 575px) {
        min-width: 350px;
    }
   
}